.albumPhoto {
  max-width: 44px;
  max-height: 44px;
}

.bx--search {
  margin: 0 !important;
  width: 100% !important;
}

@media (min-width: 66rem) {
  .bx--col-lg-10 {
    max-width: 82.6%;
  }
}

img {
  object-fit: cover;
  max-width: 36px;
  max-height: 36px;
  margin: 6px;
}

pre {
  margin-left: 2rem;
  color: red;
}

.queue-track {
  margin-bottom: 1.6rem;
  height: 3rem;
}

.queue-track > div {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.queue-track-vote-col > div > svg {
  cursor: pointer;
}

.queue-track-vote-col > div:nth-child(1) {
  margin-bottom: -1rem;
}

.queue-track-vote-col > div:nth-child(3) {
  margin-top: -0.8rem;
}

.queue-track-vote-col > div:nth-child(1) > svg[class*="active"] {
  fill: #24a148;
}

.queue-track-vote-col > div:nth-child(3) > svg[class*="active"] {
  fill: #da1e28;
}

html,
body {
  height: 100vh;
}

.bx--grid {
  max-width: none;
  padding: 0;
  margin: 0;
  margin-top: -1px;
}

.bx--header {
  position: static;
}
.bx--header__nav {
  display: block;
}
.bx--search-magnifier {
  z-index: 0;
}

#DesktopQueueWrapper,
#MobileQueueWrapper {
  height: calc(100vh - 48px);
  background-color: #262626;
  color: #f4f4f4;
  padding-left: 2rem;
  padding-right: 0.4rem;
}

.bx--search {
  margin: auto;
  width: 80%;
}

